.head-container{
    padding: 7% 6% 0;
    font-family: 'Mulish', sans-serif;
    letter-spacing: 0.03rem;
    line-height: 1.7;
    background-image: url("data:image/png;base64,");
    background-position: 10px 70px;
    background-repeat: no-repeat;

}


.github,.linkedin,.insta{
    color: gray;
    margin-bottom: 3rem;
    cursor: pointer;

}

.github:hover,.linkedin:hover,.insta:hover{
    color: black;

}

.linkedin,.insta{
    margin-left: 1.5rem;
}

.head-container .container{
    padding-left: 2rem;
}

.head-container h1{
    font-weight:700;
    font-size: 3.75rem;
}

.head-container span{
    color:lightblue;
}

.head-container .img-frame{
    width: 100%;
    height: 500px;
    background-image: url("../assets/Untitled\ design.png");
    background-size: cover;
    background-position: 50% 40%;
    border-radius: 4px;
    margin-top: 4rem;
}



@media screen and (max-width: 600px) {
    .head-container h1{
        font-size: 8vw;
    }
  }